<template>
  <el-form v-model="form" label-width="120px" style="width: 400px" v-if="!edit">
    <el-form-item label="社团名称">
      {{form.nickName}}
    </el-form-item>
    <el-form-item label="用户名">
      {{form.username}}
    </el-form-item>

    <el-form-item label="负责人姓名">
      {{form.leader}}
    </el-form-item>
    <el-form-item label="负责人联系方式">
      {{form.tel}}
    </el-form-item>
    <el-form-item label="负责人所在班级">
      {{form.class}}
    </el-form-item>
    <el-form-item>
      <el-button @click="doChange">修改</el-button>
    </el-form-item>
  </el-form>
  <el-form v-model="form" label-width="120px" style="width: 400px" v-else-if="edit">
    <el-form-item label="社团名称">
      <el-input v-model="form.nickName"></el-input>
    </el-form-item>
    <el-form-item label="用户名">
      <el-input v-model="form.username"></el-input>
    </el-form-item>
    <el-form-item label="新密码">
      <el-input v-model="form.newPassword"></el-input>
    </el-form-item>
    <el-form-item label="负责人姓名">
      <el-input v-model="form.leader"></el-input>

    </el-form-item>
    <el-form-item label="负责人联系方式">
      <el-input v-model="form.tel"></el-input>

    </el-form-item>
    <el-form-item label="负责人所在班级">
      <el-input v-model="form.class"></el-input>

    </el-form-item>
    <el-form-item>
      <el-button @click="saveChange">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "Information",
  data(){
    return {
      edit:false,
      form:{
        id:'111222',
        nickName:"erp沙盘协会",
        username:'sssspp',
        password:'pppssss',
        newPassword:'',
        leader:'妥妥',
        tel:'13800000',
        class:'工管'
      }
    }
  },
  methods:{
    doChange(){
      this.edit = !this.edit
    },
    saveChange(){
      this.doChange();
      this.form.password = this.form.password
      this.form.newPassword = '';
    }
  }
}
</script>

<style scoped>
.edit{
  border: 0px;
  outline:none;
}
</style>