<template>
	<el-dialog  :visible.sync="dialogVisible" width="40%" :before-close="handleClose">
    <h2 v-text="notice.title" style="text-align: center"></h2>
    <div v-html="notice.content" style="min-height: 500px"></div>
	</el-dialog>
</template>

<script>

	export default {
		components: {

		},
		methods: {
			handleClose() {
				this.dialogVisible = false;
			},

			openDialog() {

			  this.$axios.get("/notice/notice?id="+this.noticeId).then(res=>{
          if(res.data.code == 500){
            this.$message(res.data.message)
            return
          }
          this.notice = res.data.data ;
        })
				this.dialogVisible = true
			},

		},
		data() {
			return {
				dialogVisible: false,
        noticeId:0,
        notice:{}
			}
		}
	}
</script>

<style>
</style>
